import Vue from 'vue'
import App from 'App.vue'
import Home from 'views/home.vue'

import VueRouter from 'vue-router'

Vue.use(VueRouter)

const router = new VueRouter()

router.map({
  '/': {
    component: Home
  }
})

const app = new Vue({
  router
}).$mount('#app')



